*,
*::after,
*::before {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:root {
    --font1: 'Open Sans', sans-serif;
}

html {
    font-size: 62.5%;
}

.curtain {
    width: 100vw;
    height: 100vh;
}

.curtain video {
    object-fit: cover;
    width: 100vw;
    height: 100vh;
}

.touch-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 10vw;
    height: 7vh;
}

.year {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: year 5s ease-in-out;
}

.container {
    width: 50vw;
    height: 80vh;
    border: 3px solid red;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: newYear 10s ease-in-out;
    overflow: hidden;
}

.container h1 {
    font-size: 5rem;
    font-family: var(--font1);
    color: green;
}

@keyframes year {
    0% {
        background-color: #39FF14;
    }
    100% {
        background-color: transparent;
    }
}

@keyframes newYear {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes rotate-border {
    100% {
        transform: rotate(360deg);
    }
}